import { mapGetters, mapActions } from 'vuex';
import pageHead from '@/component/page-header.vue';

export default {
  components: {
    pageHead
  },
  name: 'centerMsg',
  data() {
    return {
      msg: [
        {
          url: 'javascript:;',
          date: '12-04 11:23',
          title: '活动通知',
          content: '亲爱的XX，您报名XX活动即将开始！',
          hasread: false, //是否已读
          article: true //是否可点开详情
        },
        {
          url: 'javascript:;',
          date: '12-04 11:23',
          title: '培训通知',
          content: '亲爱的XX，您报名XX活动即将开始！',
          hasread: true,
          article: true
        },
        {
          url: 'javascript:;',
          date: '12-04 11:23',
          title: '',
          content: '亲爱的XX，您报名XX活动即将开始！',
          hasread: true,
          article: false
        }
      ]
    };
  },
  methods: {
    ...mapActions([])
  },
  watch: {
    getReadAll(readAll){   
      if(readAll) {
        this.msg.forEach(e => {
          e.hasread = true;
        })
      }else {
        vuex.dispatch('AgetreadAll', false);
      }
    }
  },
  computed: {
    ...mapGetters(['readAll']),
    getReadAll(){
      return this.readAll;
    }
  },
  created() {},
  mounted() {
    let elem = document.querySelector('#vux_view_box_body');
    setTimeout(() => {
      elem.style.paddingBottom = '0';
    }, 10);
  }
};
